html {
  font-size: 62.5%;
}
body {
  font: 1.4rem "Century Gothic", Futura, sans-serif;
}

header {
  width: 100%;
  margin: 4.8rem 0;
  -ms-flex-preferred-size: 6rem;
      flex-basis: 6rem;
  text-align: center;
  font-size: 5.6rem;
}

footer {
  width: 100%;
  margin: 2.8rem 0;
  -ms-flex-preferred-size: 6rem;
      flex-basis: 6rem;
  text-align: center;
  font-size: 2.4rem;
}

.app {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.board-row {
  max-width: 480px;
  width: 80%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 1rem;
}

.square {
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
  margin-top: -0.1rem;
  margin-right: -0.1rem;
  border: 0.1rem solid #999;
  padding: 0;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.square::after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.square:focus {
  outline: none;
}

.square-content {
  position: absolute;
  font-size: 10vw;
  font-weight: bold;
  text-align: center;

}

.square--winner {
  background-color: #aaa;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.game-board {
  -ms-flex: 2 1;
      flex: 2 1;
}

.game-info {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-left: 2rem;
}

.game-status {
  font-size: 2.4rem;
  margin-bottom: 1.2rem;
}

.move-controller {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1.2rem;
}

.move-controller > button {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 1.2rem;
  font-size: 1.8rem;
}



